$daySize: 100px;
$border: 1px solid black;
$gap: 1px;
$padding: 10px;
$columns: 7;
$calendarWidth: ($daySize * 7) + ($gap) + (10 * 2);
$background-color: #eee;
$animation-duration: 0.5s;
$border-width: 1px;
$days: (
  mon, tue, wed, thu, fri, sat, sun
);
$dayOrder: (
  mon: 7,
  tue: 6,
  wed: 5,
  thu: 4,
  fri: 3,
  sat: 2,
  sun: 1,
);
